.header {
  //height: 146px;
  height: 102px;
  display: flex;
  align-items: center;
  background: #fff;
  z-index: 2;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;

  @media(max-width: 600px) {
    height: 60px;
    padding: 10px 0;

    .container {
      height: 100%;
    }
  }

  &__content {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @media(max-width: 600px) {
      height: 100%;
    }
  }

  &__nav {
    display: flex;
    //margin-right: 240px;
    margin-right: 180px;
    margin-left: auto;

    @media(max-width: 1240px) {
      margin-right: 60px;
    }

    @media(max-width: 600px) {
      display: none;
    }

    &-item {
      font-style: normal;
      font-weight: 700;
      //font-size: 16px;
      font-size: 17px;
      line-height: 120%;
      color: #1D1860;
      position: relative;

      @media(max-width: 1240px) {
        font-size: 15px;
      }

      &:before {
        content: '';
        position: absolute;
        bottom: -2px;
        left: 0;
        width: 0;
        height: 2px;
        background: #1D1860;
        transition: .3s ease;
        overflow: hidden;
      }

      &:hover {
        
        &:before {
          width: 100%;
        }
      }

      &:not(:last-of-type) {
        margin-right: 50px;
      }
    }
  }

  &__buttons {
    display: flex;
    align-items: center;

    &-link {
      font-weight: 700;
      font-size: 20px;
      line-height: 120%;
      color: #1D1860;
      margin-right: 52px;
    }

    &-btn {
      font-weight: 700;
      font-size: 20px;
      line-height: 150%;
      color: #1D1860;
      padding: 18px 59px;
      background: #57ECA7;
      border-radius: 50px;

      &.orange {
        background: #FF9A51;

        &:hover {
          background: #ff903f;
        }
      }

      &.purple {
        background: #A091FA;
        color: #FFFFFF;

        &:hover {
          background: #7361fd;
          color: #FFFFFF;
        }
      }

      @media(max-width: 600px) {
        font-size: 16px;
        padding: 10px 24px;
      }

      &:hover {
        background: #53de9e;
      }
    }
  }

  @media(max-width: 600px) {
    .header__logo {
      height: 90%;
      display: flex;
      box-sizing: border-box;

      img {
        height: 100%;
        width: auto;
      }
    }
  }
}