.footer {
  //padding: 0 178px 57px;
  padding: 0 104px 40px;
  display: flex;
  align-items: center;

  @media(max-width: 600px) {
    padding: 0 10px 40px;
    flex-direction: column;
  }

  &__nav {
    display: flex;

    @media(max-width: 600px) {
      flex-direction: column;
      align-items: center;
      margin-bottom: 40px;
    }

    &-item {
      font-weight: 700;
      font-size: 16px;
      line-height: 150%;
      color: #1d1860;

      @media(max-width: 600px) {
        text-align: center;
      }

      &:not(:last-of-type) {
        margin-right: 41px;

        @media(max-width: 600px) {
          margin-right: 0;
          margin-bottom: 10px;
        }
      }
    }
  }

  &__contacts {
    display: flex;
    margin-left: auto;

    @media(max-width: 600px) {
      flex-direction: column;
      margin-right: auto;
      align-items: center;
    }

    &-item {
      display: flex;
      align-items: center;
      font-weight: 600;
      font-size: 16px;
      line-height: 150%;
      color: #1D1860;

      &:not(:last-of-type) {
        margin-right: 48px;

        @media(max-width: 600px) {
          margin-right: 0;
          margin-bottom: 10px;
        }
      }

      a {
        color: #1D1860;
      }

      svg {
        width: 20px;
        height: 20px;
        margin-right: 8px;
      }
    }
  }
}