h1, h2, h3, h4, h5, h6, ul, p {
    margin: 0;
    padding: 0;
}

body, html {
    font-family: 'TT Hoves', sans-serif;
    font-weight: 400;
    color: #1D1860;
    background: #fff;
    overflow-x: hidden;
    min-height: 100vh;
}

body {
    display: flex;
    flex-direction: column;
}

.container {
    width: 100%;
    max-width: 1400px;
    padding: 0 20px;
    margin: 0 auto;
    box-sizing: border-box;

    @media(max-width: 600px) {
        padding: 0 10px;
    }
}

.container-fluid {
    width: 100%;
    padding: 0;
    box-sizing: border-box;
}

.row {
    margin: 0 -20px;

    @media(max-width: 600px) {
        margin: 0 -10px;
    }
}

a {
    text-decoration: none;
    transition: .3s ease;
    white-space: nowrap;

    &:hover, &:focus, &:focus {
        color: inherit;
    }
}

.page {
    //padding-top: 120px;
    padding-top: 84px;
    width: 100%;
    //margin: 146px auto 0;
    margin: 96px auto 0;
    display: flex;
    flex-grow: 1;
    justify-content: center;

    @media(max-width: 600px) {
        margin-top: 60px;
        padding-top: 40px;
    }

    &__content {
        display: flex;
        flex-direction: column;
        width: 100%;
    }
}

.title {
    display: flex;
    align-self: flex-start;

    @media(max-width: 600px) {
        align-self: center;
    }

    h1, h2, h3, h4 {
        font-weight: 400;
        font-size: 4em;
        //font-size: 96px;
        line-height: 130%;

        @media(max-width: 600px) {
            font-size: 2.1em;
            text-align: center;
        }
    }

    &-bg {
        position: relative;
        //padding: 23px 224px 16px 42px;
        padding: 12px 157px 11px 30px;

        @media(max-width: 600px) {
            padding: 2px 18px 2px 10px;

        }

        h1, h2, h3, h4 {
            z-index: 1;

            @media(max-width: 600px) {
                text-align: left;
            }
        }

        &:before {
            content: '';
            position: absolute;
            //height: 156px;
            height: 90px;
            background: #E6E2FF;
            top: 0;
            left: 0;
            width: 100%;

            @media(max-width: 600px) {
                height: 50px;
            }
        }

        &:after {
            content: '';
            position: absolute;
            //height: 156px;
            height: 109px;
            background: #E6E2FF;
            top: 90px;
            left: 0;
            //width: calc(100% - 224px);
            width: calc(100% - 156px);

            @media(max-width: 600px) {
                width: calc(100% - 14px);
                height: 50px;
                top: 50px;
            }
        }

        &.title-v2 {
            margin-left: -30px;

            @media(max-width: 600px) {
                margin-left: 0;
                padding-right: 0;
            }

            &:before {
                width: 471px;
                background: #F2F5FF;

                @media(max-width: 600px) {
                    width: 50%;
                }
            }

            &:after {
                width: 615px;
                background: #F2F5FF;

                @media(max-width: 600px) {
                    width: 80%;
                }
            }
        }
    }

    &-product {

        @media(max-width: 600px) {
            font-size: 12px;
            margin-right: auto;
        }

        &:before {
            width: 410px;

            @media(max-width: 600px) {
                width: 50%;
                height: 35px;
            }
        }

        &:after {
            width: 660px;

            @media(max-width: 600px) {
                top: 35px;
                width: 80%;
                height: 35px;
            }
        }
    }

    &-single-line {
        //padding-top: 15px;
        padding-top: 11px;
        //padding-right: 104px;
        padding-right: 73px;

        &:after {
            display: none;

            @media(max-width: 600px) {
                display: block;
                width: 70%;
            }
        }

        &:before {
            height: 100%;
        }

        @media(max-width: 600px) {

            &:before {
                width: 80%;
            }
        }
    }
    
    &-ai {

        &:before {
            width: 30%;
        }

        &:after {
            width: 49%;
        }
    }
}

.description {
    //font-size: 40px;
    font-size: 28px;
    line-height: 130%;
    //max-width: 994px;
    max-width: 696px;
    //margin-top: 32px;
    margin-top: 22px;

    @media(max-width: 600px) {
        max-width: 100%;
        width: 100%;
        font-size: 22px;
        text-align: center;
    }

    span {
        font-weight: 700;
    }
}

section {
    display: flex;
    //margin-bottom: 120px;
    margin-bottom: 84px;

    @media(max-width: 600px) {
        margin-bottom: 60px;
    }

    &.inc-padding {
        //padding-left: 42px;
        padding-left: 0;
    }
}

.first {
    flex-direction: column;

    &__top {
        display: flex;
        //margin-bottom: 120px;
        margin-bottom: 84px;

        @media(max-width: 1240px) {
            h1 {
                font-size: 3.6em;
            }
        }

        @media(max-width: 600px) {
            flex-direction: column;
            margin-bottom: 40px;
        }
    }

    &__btn {
        background: #1D1860;
        border-radius: 134px;
        //font-size: 48px;
        font-size: 34px;
        line-height: 130%;
        color: #E6E2FF;
        //padding: 47px 137px;
        padding: 33px 96px;
        display: flex;
        align-items: center;
        align-self: flex-end;
        margin-left: auto;

        @media(max-width: 600px) {
            margin-top: 30px;
            font-size: 20px;
            padding: 22px 60px;
            align-self: center;
            text-align: center;
            margin-right: auto;
        }

        &:hover, &:focus {
            background: #251f7b;
            color: #E6E2FF;
        }
    }

    &__text {
        display: flex;
        position: relative;

        @media(max-width: 600px) {
            flex-direction: column;
        }

        p {
            //max-width: 994px;
            max-width: 696px;
            //font-size: 40px;
            font-size: 28px;
            line-height: 130%;
            //padding-left: 42px;
            padding-left: 29px;
            position: absolute;
            top: 0;
            left: 0;

            @media(max-width: 1240px) {
                max-width: 55%;
            }

            @media(max-width: 600px) {
                max-width: 100%;
                width: 100%;
                font-size: 22px;
                padding-left: 0;
                position: relative;
            }
        }

        &-deal {
            //width: 945px;
            width: 662px;
            //height: 328px;
            height: 230px;
            //padding-right: 159px;
            padding-right: 111px;
            background: url("../img/linear-green-bg.png") no-repeat right bottom;
            background-size: cover;
            display: flex;
            justify-content: flex-end;
            align-self: flex-end;
            margin-left: auto;
            //margin-top: 72px;
            margin-top: 50px;

            @media(max-width: 600px) {
                padding-right: 0;
                width: 100%;
                height: auto;
                justify-content: center;
            }

            &--text {
                display: flex;
                flex-direction: column;
                align-self: flex-end;
                z-index: 1;
                //margin-bottom: 52px;
                margin-bottom: 36px;
                //font-size: 40px;
                font-size: 28px;
                line-height: 120%;
                text-align: center;

                span {
                    //font-size: 128px;
                    font-size: 90px;
                    line-height: 150%;
                    //margin-bottom: -20px;
                    margin-bottom: -14px;
                    //padding-left: 20px;
                    padding-left: 14px;
                }
            }
        }
    }
}

.examples {
    display: flex;
    flex-direction: column;

    &__items {
        //margin-left: 155px;
        margin-left: 109px;
        display: flex;
        background: #E6E2FF;

        @media(max-width: 600px) {
            margin-left: 0;
            flex-direction: column;
            margin-top: 16px;
            background: none;
        }
    }

    &__item {
        display: flex;
        flex-direction: column;
        align-items: center;
        flex-grow: 1;
        //padding: 87px 63px;
        padding: 61px 44px;

        @media(max-width: 600px) {
            padding: 30px 20px;
            background: #E6E2FF;

            &:not(:last-of-type) {
                margin-bottom: 20px;
            }
        }

        &:nth-of-type(2) {
            background: #1D1860;

            p {
                color: #FFFFFF;
            }
        }

        img {
            max-width: 100%;
        }

        p {
            //margin-top: 34px;
            margin-top: 24px;
            text-align: center;
            //font-size: 40px;
            font-size: 28px;
            line-height: 130%;

            @media(max-width: 600px) {
                font-size: 24px;
            }
        }
    }
}

.steps {
    flex-direction: column;
    position: relative;

    &__bg {
        position: absolute;
        //top: 185px;
        top: 130px;
        //right: -76px;
        right: -53px;
        z-index: 1;
        width: 247px;

        @media(max-width: 600px) {
            display: none;
        }

        &.inapp-steps {
            top: 380px;
            right: auto;
            left: -60px;
            transform: rotate(180deg);
        }
    }

    &__road {
        //margin-top: 132px;
        margin-top: 92px;
        //padding: 0 22px;
        padding: 0 16px;

        @media(max-width: 600px) {
            padding: 0;
            margin-top: 40px;
        }

        &-group {
            display: flex;
            height: 300px;
            position: relative;

            @media(max-width: 600px) {
                height: auto;
                flex-direction: column;

                &:not(:last-of-type) {
                    padding-bottom: 40px;
                }
            }

            &:before {
                content: '';
                position: absolute;
                left: 0;
                right: 350px;
                //top: 36px;
                top: 25px;
                height: 3px;
                background: #292375;

                @media(max-width: 600px) {
                    display: none;
                }
            }

            &:after {
                content: '';
                position: absolute;
                right: 0;
                //top: 36px;
                top: 25px;
                border: 3px solid #292375;
                border-radius: 0 100% 100% 0 / 0 350px 350px 0;
                border-left: 0;
                width: 350px;
                height: calc(100% - 3px);

                @media(max-width: 600px) {
                    display: none;
                }
            }

            &--reverse {
                flex-direction: row-reverse;

                &:before {
                    left: 350px;
                    right: 350px;
                }

                &:after {
                    border-radius: 350px 0 0 350px / 100% 0 0 100%;
                    border-right: 0;
                    border-left: 3px solid #292375;
                    left: 0;
                    right: auto;
                }
            }

            &:nth-of-type(3n) {

                &:before {
                    left: 350px;
                }
            }

            &:last-of-type {
                height: auto;

                &:before {
                    right: 0;
                }

                &:after {
                    display: none;
                }
            }
        }

        &-item {
            display: flex;
            flex-direction: column;
            z-index: 1;

            @media(max-width: 600px) {
                margin-left: auto !important;
                margin-right: auto !important;
                align-items: center;

                &:not(:last-of-type) {
                    margin-bottom: 30px;
                }
            }

            &--num {
                //width: 216px;
                width: 151px;
                //height: 74px;
                height: 52px;
                background: #292375;
                color: #FFFFFF;
                //font-size: 40px;
                font-size: 28px;
                //line-height: 52px;
                line-height: 36px;
                display: flex;
                align-items: center;
                //padding-left: 40px;
                padding-left: 28px;
                align-self: flex-start;
                flex-shrink: 0;

                @media(max-width: 600px) {
                    align-self: center;
                    text-align: center;
                    justify-content: center;
                    font-size: 22px;
                    height: auto;
                    width: auto;
                    padding: 8px 26px;
                    border-radius: 10px;
                }
            }

            &--text {
                //font-size: 30px;
                font-size: 21px;
                line-height: 130%;
                //padding-left: 40px;
                padding-left: 28px;
                //margin-top: 45px;
                margin-top: 32px;

                @media(max-width: 600px) {
                    padding-left: 0;
                    margin-top: 26px;
                    text-align: center;
                    font-size: 18px;
                }
            }

            &--1 {
                //margin-left: 143px;
                margin-left: 100px;

                .steps__road-item {

                    &--num {
                        background: #5AF4AD;
                        color: #1B1446;
                    }
                }
            }

            &--reversed {

                @media(min-width: 601px) {
                    flex-direction: column-reverse;
                    margin-bottom: auto;
                    margin-top: -86px;

                    .steps__road-item {

                        &--text {
                            margin-top: 0;
                            margin-bottom: 32px;
                        }
                    }
                }
            }

            &--2 {
                //margin-left: 234px;
                margin-left: 164px;
            }

            &--3 {
                //margin-right: 127px;
                margin-right: 89px;
            }

            &--4 {
                //margin-right: 250px;
                margin-right: 175px;
            }

            &--5 {
                //margin-left: 143px;
                margin-left: 25%;
            }

            &--6 {
                //margin-left: 280px;
                margin-left: 156px;
            }

            &--btn {
                //margin-left: 117px;
                margin-left: auto;
                align-self: flex-start;
                //margin-top: -20px;
                margin-top: -14px;

                @media(max-width: 600px) {
                    margin-top: 20px;
                }

                a {
                    //font-size: 40px;
                    font-size: 28px;
                    //padding: 34px 97px;
                    padding: 24px 68px;
                    background: #5AF4AD;
                    border-radius: 134px;
                    line-height: 1;
                    color: #1d1860;

                    @media(max-width: 600px) {
                        font-size: 24px;
                        padding: 22px 56px;
                    }

                    &:hover, &:focus {
                        background: #53de9e;
                    }
                }
            }
        }
    }
}

.use {
    flex-direction: column;
    position: relative;

    &.orange {

        &:before {
            background: #FF9A51;
        }
    }

    &:before {
        content: '';
        position: absolute;
        //bottom: 60px;
        bottom: 42px;
        //left: -85px;
        left: -20px;
        //right: -76px;
        right: -20px;
        //height: 370px;
        height: 259px;
        background: #5AF4AD;

        @media(max-width: 600px) {
            display: none;
        }
    }

    &__items {
        display: flex;
        //margin-top: 108px;
        margin-top: 76px;
        z-index: 1;
        margin-bottom: 46px;

        @media(max-width: 600px) {
            flex-direction: column;
        }
    }

    &__item {
        display: flex;
        flex-direction: column;
        flex: 1 1 0;
        background: #292375;
        //padding: 0 70px 68px 35px;
        padding: 0 49px 48px 25px;

        @media(max-width: 600px) {
            padding: 0 20px 40px;
            border-radius: 10px;
        }

        &:not(:last-of-type) {
            margin-right: 40px;

            @media(max-width: 600px) {
                margin-right: 0;
                margin-bottom: 70px;
                align-items: center;
            }
        }

        img {
            align-self: flex-start;
            margin-top: -48px;
            margin-bottom: 36px;

            @media(max-width: 600px) {
                max-width: 70%;
                margin-top: -36px;
                align-self: center;
            }
        }

        &-title {
            //font-size: 40px;
            font-size: 28px;
            line-height: 130%;
            color: #fff;
            font-weight: 700;
            //margin-bottom: 32px;
            margin-bottom: 23px;

            @media(max-width: 600px) {
                text-align: center;
            }
        }

        p {
            //font-size: 24px;
            font-size: 17px;
            line-height: 130%;
            color: #FFFFFF;

            @media(max-width: 600px) {
                text-align: center;
            }
        }
    }

    &__bottom {
        display: flex;
        justify-content: space-between;
        z-index: 1;

        @media(max-width: 600px) {
            flex-direction: column-reverse;
        }

        a {
            //margin-top: 53px;
            margin-top: 37px;
            background: #292375;
            border-radius: 134px;
            //font-size: 40px;
            font-size: 28px;
            line-height: 130%;
            color: #FFFFFF;
            //padding: 34px 78px;
            padding: 24px 55px;

            @media(max-width: 600px) {
                margin-top: 30px;
                padding: 20px 40px;
                font-size: 24px;
                text-align: center;
            }

            &:hover, &:focus {
