            }
        }

        p {
            //padding-right: 60px;
            padding-right: 42px;
            //font-size: 24px;
            font-size: 17px;
            line-height: 130%;

            @media(max-width: 600px) {
                font-size: 18px;
                text-align: center;
                padding-right: 0;
            }
        }
    }
}

.plans {
    flex-direction: column;

    &.orange {

        .plans {

            &__group {

                &:first-of-type {
                    .plans__group {
                        &-heading {
                            background: #FF9A51;
                        }
                    }
                }

                &:last-of-type {

                    .plans__group {

                        &-item {

                            &-item--btn {
                                background: #FEA362;
                                color: #1D1860;

                                &:hover, &:focus {
                                    background: #FEA362;
                                    color: #1D1860;
                                }
                            }
                        }

                        &-item:not(:first-of-type) {

                            .plans__group-item--btn {
                                background: #FEA362;
                                color: #1D1860;

                                &:hover, &:focus {
                                    background: #fa9a52;
                                    color: #1D1860;
                                }
