            text-align: center;
            height: auto;
            padding: 20px 5px 20px 30px;
        }
    }

    &__img {
        position: absolute;
        right: 93px;
        top: 70px;

        @media(max-width: 600px) {
            position: relative;
            right: auto;
            top: auto;
        }
    }

    &__items {
        display: flex;
        flex-direction: column;
        margin-top: 36px;
        width: 42%;

        @media(max-width: 600px) {
            width: 100%;
            margin-top: 30px;
        }
    }

    &__item {
        display: flex;
        align-items: center;

        &:not(:last-of-type) {
            margin-bottom: 56px;

            @media(max-width: 600px) {
                margin-bottom: 30px;
            }
        }

        &-img {
            margin-right: 56px;
            width: 115px;
            flex-shrink: 0;
            display: flex;
            align-items: center;
            justify-content: center;

            @media(max-width: 600px) {
                width: 60px;
                margin-right: 20px;
            }

            img {
                max-width: 100%;
            }
        }

        p {
            font-size: 28px;
            line-height: 130%;

            @media(max-width: 600px) {
                font-size: 18px;
            }
        }
    }
}

