.faq {
  flex-direction: column;

  .title {
    //height: 289px;
    height: 202px;

    h4 {
      background: url("../img/index/faq-title-bg.png");
      background-size: cover;
      color: #fff;
      //padding: 20px 35px 0 57px;
      padding: 14px 25px 0 40px;
    }
  }

  &__items {
    display: flex;
    flex-direction: column;
    //margin-top: 118px;
    margin-top: 83px;
  }

  &__item {
    display: flex;
    flex-direction: column;
    //max-width: 1525px;
    max-width: 80%;

    &:not(:last-of-type) {
      //margin-bottom: 140px;
      margin-bottom: 98px;
    }

    &:nth-of-type(odd) {

      .faq__item-question {
        background: #E2DDFF;
      }
    }

    &:nth-of-type(even) {
      //margin-left: 147px;
      margin-left: 103px;

      .faq__item-question {
        background: #6EF6B7;
      }
    }

    &.active {

      .faq__item {

        &-question {

          svg {
            transform: rotate(90deg);
            right: -12px;

            use {
              fill: #1D1860
            }
          }
        }
      }
    }

    &-question {
      //padding: 38px 155px 37px 35px;
      padding: 27px 109px 26px 25px;
      flex-grow: 1;
      //font-size: 48px;
      font-size: 34px;
      line-height: 130%;
      position: relative;
      cursor: pointer;

      div {

        span {
          font-weight: 700;
        }
      }

      svg {
        //width: 111px;
        width: 78px;
        //height: 129px;
        height: 90px;
        position: absolute;
        //bottom: -60px;
        bottom: -42px;
        //right: -20px;
        right: -14px;
        transition: .3s;

        use {
          transition: .3s;
          fill: #A091FA;
        }
      }
    }

    &-answer {
      //padding-top: 50px;
      padding-top: 35px;
      //font-size: 40px;
      font-size: 28px;
      line-height: 130%;
      align-self: center;
      max-width: 80%;
      display: none;
    }
  }
}