.contactForm {
  //background: url("../img/contact-form-bg.png") top 74px right no-repeat, #1D1860;
  background: url("../img/contact-form-bg.png") top 52px right no-repeat, #1D1860;
  background-size: 574px 340px;
  //padding: 60px 250px 110px 85px;
  padding: 42px 175px 77px 60px;
  display: flex;
  flex-direction: column;
  flex-shrink: 0;

  @media(max-width: 600px) {
    background: #1D1860;
    padding: 40px 40px 50px;
  }

  &.orange {

    .contactForm__form-btn {
      background: #FF9A51;

      &:focus, &:hover {
        outline: 0;
        background: #ff9342;
      }
    }
  }

  h4 {
    //font-size: 80px;
    font-size: 56px;
    line-height: 130%;
    color: #E6E2FF;
    font-weight: 400;
    //margin-bottom: 84px;
    margin-bottom: 59px;

    @media(max-width: 600px) {
      font-size: 28px;
      text-align: center;
      margin-bottom: 40px;

      br {
        display: none;
      }
    }
  }

  &__form {

    &-wrapper {
      display: flex;
      flex-shrink: 0;

      @media(max-width: 600px) {
        flex-direction: column;
      }
    }

    &-inputs {
      display: flex;
      flex-direction: column;
      //width: 885px;
      width: 620px;

      @media(max-width: 600px) {
        width: 100%;
      }
    }

    &-input {
      width: 100%;
      display: flex;

      > p {
        width: 100%;
        display: flex;
      }

      &:not(:last-of-type) {
        //margin-bottom: 60px;
        margin-bottom: 42px;

        @media(max-width: 600px) {
          margin-bottom: 20px;
        }
      }
    }

    &-right {
      //margin-left: 240px;
      margin-left: 168px;
      align-self: flex-end;
      flex-grow: 1;

      @media(max-width: 600px) {
        margin-left: 0;
        align-self: center;
        width: 100%;
        margin-top: 20px;
      }
    }

    &-btn {
      background: #5AF4AD;
      border-radius: 134px;
      //padding: 47px 200px !important;
      padding: 33px 140px !important;
      font-weight: 700;
      //font-size: 48px !important;
      font-size: 34px !important;
      line-height: 130% !important;
      color: #1D1860 !important;
      cursor: pointer;
      border: 0;
      transition: .3s;
      height: auto !important;

      @media(max-width: 600px) {
        font-size: 26px !important;
        padding: 0 20px !important;
        height: 60px !important;
      }

      &:focus, &:hover {
        outline: 0;
        background: #4dd094;
      }
    }
  }

  input {
    //height: 137px;
    height: 96px;
    //font-size: 40px;
    font-size: 28px;
    line-height: 130%;
    //padding: 0 93px;
    padding: 0 45px;
    color: rgba(#1B1446, .8);
    outline: 0;
    box-sizing: border-box;
    width: 100%;

    @media(max-width: 600px) {
      padding: 0 20px;
      font-size: 22px;
      height: 60px;
      border-radius: 10px;
    }

    &::placeholder {
      color: rgba(#1B1446, .2);
    }
  }
}